import { createSlice, Draft, PayloadAction } from '@reduxjs/toolkit';
import { AlloyInstance, AlloySkolem } from 'alloy-ts';
import { setInstance, setLoop } from '../../sterling/sterlingSlice';
import {
    HorizontalAlignment,
    LayoutDirection,
    SigFieldSkolem,
    SKOLEM_COLORS,
    SortDirection,
    SortMethod,
    SortType,
    TablesType
} from './tableTypes';

// Table view state
export interface TableState {
    alignment: HorizontalAlignment
    collapseData: boolean
    collapseLayout: boolean
    collapseTables: boolean
    currentInstance: number
    data: SigFieldSkolem[]
    dataSelected: SigFieldSkolem[]
    highlightSkolems: boolean
    instances: AlloyInstance[]
    layoutDirection: LayoutDirection
    localNecessity: string | null
    loop: string | null
    removeBuiltin: boolean
    removeEmpty: boolean
    removeThis: boolean
    primarySort: SortType
    secondarySort: SortType
    skolemColors: Map<AlloySkolem, string>
    tablesType: TablesType
}

// The initial table view state
const initialState: TableState = {
    alignment: HorizontalAlignment.Left,
    collapseData: false,
    collapseLayout: false,
    collapseTables: false,
    currentInstance: 0,
    data: [],
    dataSelected: [],
    highlightSkolems: true,
    instances: [],
    layoutDirection: LayoutDirection.Row,
    localNecessity: null,
    loop: null,
    removeBuiltin: true,
    removeEmpty: false,
    removeThis: true,
    primarySort: {
        method: SortMethod.Group,
        direction: SortDirection.Descending
    },
    secondarySort: {
        method: SortMethod.Size,
        direction: SortDirection.Descending
    },
    skolemColors: new Map(),
    tablesType: TablesType.All
};

// The table slice
const tableSlice = createSlice({
    name: 'table',
    initialState: initialState,
    reducers: {
        clearSelectedData (state) {
            state.dataSelected = []
        },
        deselectData (state, action: PayloadAction<SigFieldSkolem>) {
            const index = state.dataSelected.indexOf(action.payload);
            if (index !== -1) {
                state.dataSelected.splice(index, 1);
            }
            state.tablesType = TablesType.Select;
        },
        nextTime (state) {
            if (state.currentInstance < state.instances.length - 1) {
                state.currentInstance += 1;
                const instance = state.instances[state.currentInstance];
                if (instance) setCurrentInstance(state, instance as AlloyInstance);
            }
        },
        previousTime (state) {
            if (state.currentInstance > 0) {
                state.currentInstance -= 1;
                const instance = state.instances[state.currentInstance];
                if (instance) setCurrentInstance(state, instance as AlloyInstance);
            }
        },
        selectData (state, action: PayloadAction<SigFieldSkolem>) {
            if (!state.dataSelected.includes(action.payload)) {
                state.dataSelected.push(action.payload);
            }
            state.tablesType = TablesType.Select;
        },
        setAlignment (state, action: PayloadAction<HorizontalAlignment>) {
            state.alignment = action.payload
        },
        setLayoutDirection (state, action: PayloadAction<LayoutDirection>) {
            state.layoutDirection = action.payload
        },
        setLocalNecessity (state, action: PayloadAction<string|null>) {
            state.localNecessity = action.payload;
        },
        setSort (state, action: PayloadAction<SortType>) {
            if (state.primarySort.method !== action.payload.method) {
                state.secondarySort = state.primarySort;
            }
            state.primarySort = action.payload;
        },
        setTableTypes (state, action: PayloadAction<TablesType>) {
            state.tablesType = action.payload
        },
        toggleCollapseData (state) {
            state.collapseData = !state.collapseData
        },
        toggleCollapseLayout (state) {
            state.collapseLayout = !state.collapseLayout
        },
        toggleCollapseTables (state) {
            state.collapseTables = !state.collapseTables
        },
        toggleHighlightSkolems (state) {
            state.highlightSkolems = !state.highlightSkolems;
            if (state.highlightSkolems && state.tablesType === TablesType.Skolems) {
                state.tablesType = TablesType.All;
            }
        },
        toggleRemoveBuiltin (state) {
            state.removeBuiltin = !state.removeBuiltin
        },
        toggleRemoveEmpty (state) {
            state.removeEmpty = !state.removeEmpty
        },
        toggleRemoveThis (state) {
            state.removeThis = !state.removeThis
        }
    },
    extraReducers: builder =>
        builder
            .addCase(setInstance, (state, action: PayloadAction<AlloyInstance[]>) => {

                state.instances = action.payload;
                state.currentInstance = 0;
                const instance = action.payload[state.currentInstance];

                if (instance)
                    setCurrentInstance(state, instance);

            })
            .addCase(setLoop, (state, action: PayloadAction<string | null>) => {
                state.loop = action.payload;
            })
});

function setCurrentInstance (state: Draft<TableState>, instance: AlloyInstance) {
    const skolems = instance.skolems();

    state.data = [
        ...instance.signatures(),
        ...instance.fields(),
        ...skolems
    ];

    state.skolemColors = new Map(skolems.map((skolem, i) => {
        return [skolem, SKOLEM_COLORS[i % skolems.length]];
    }));

    state.dataSelected = state.dataSelected.filter(item => {
        return state.data.find(curr => curr.id() === item.id()) !== undefined;
    })
}

export const {
    clearSelectedData,
    deselectData,
    nextTime,
    previousTime,
    selectData,
    setAlignment,
    setLayoutDirection,
    setLocalNecessity,
    setSort,
    setTableTypes,
    toggleCollapseData,
    toggleCollapseLayout,
    toggleCollapseTables,
    toggleHighlightSkolems,
    toggleRemoveBuiltin,
    toggleRemoveEmpty,
    toggleRemoveThis
} = tableSlice.actions;

export default tableSlice.reducer;
