import { Button, ButtonGroup, IconName, Navbar, NavbarDivider, Tag } from '@blueprintjs/core';
import React from 'react';
import { SterlingConnection } from '../../sterling/SterlingConnection';
import ViewGroup from './components/ViewGroup';
import { isDefined } from 'ts-is-present';

export interface NavbarCustomButton {
    text: string
    command: string
    disabled?: boolean
    icon?: IconName
}

export interface SterlingNavbarProps {
    connection: SterlingConnection
}

interface ISterlingNavbarState {
    command: string
    connected: boolean
    ready: boolean
    buttons?: NavbarCustomButton[]
}

class SterlingNavbar extends React.Component<SterlingNavbarProps, ISterlingNavbarState> {

    constructor (props: SterlingNavbarProps) {

        super(props);

        this.state = {
            command: '',
            connected: false,
            ready: false
        }

    }

    componentDidMount (): void {

        const connection = this.props.connection;

        connection
            .addEventListener('connect', () => {
                this.setState({ connected: true });
            })
            .addEventListener('disconnect', () => {
                this.setState({ connected: false, ready: false });
            })
            .addEventListener('instance', event => {
                this.setState({
                    // command: event.instance.command(),
                    ready: this.state.connected
                });
            })
            .addEventListener('buttons', event => {
                console.log('buttons event');
                this.setState({
                    buttons: event.buttons
                });
            })

    }

    render (): React.ReactNode {

        const state = this.state;

        return (
            <Navbar fixedToTop className={'nav bp3-dark'}>
                <ViewGroup/>
                <Navbar.Group className={'collapsing'}>
                    {
                        state.command.length > 0 &&
                        <Tag minimal={true}>
                            {state.command}
                        </Tag>
                    }
                    <NavbarDivider/>
                    {
                        this._buttons()
                    }
                </Navbar.Group>
            </Navbar>
        )

    }

    private _buttons = () => {
        const state = this.state;
        const buttons = state.buttons;
        const connection = this.props.connection;
        if (buttons) {
            return <ButtonGroup>
                {
                    buttons.map((button, i) =>
                        <Button
                            disabled={isDefined(button.disabled) ? button.disabled : !state.ready}
                            intent={state.connected ? 'success' : 'danger'}
                            large={true}
                            onClick={() => {connection.sendMessage(button.command)}}
                            text={button.text}
                            rightIcon={button.icon}
                            key={i}/>)
                }
            </ButtonGroup>
        } else {
            return <Button disabled={!state.ready}
                           intent={state.connected ? 'success' : 'danger'}
                           large={true}
                           onClick={this._requestNext}
                           rightIcon={'circle-arrow-right'}
                           text={'Next'}
            />
        }
    }

    private _requestNext = () => {

        this.props.connection.requestNextInstance();

    }

}

export default SterlingNavbar;
